/* ==== NAVIGATION CONTAINER ==== */
.nav-container-NavigationBar,
.navbar {
  background: linear-gradient(-9deg, #f34998, #f172a1);
  /* background-color: #f172a1; */
  opacity: 0.98;
  font-family: 'Raleway', 'Roboto Slab', Arial, Helvetica, sans-serif;
  position: -webkit-sticky;
  position: sticky;
  top: -1%;
  z-index: 100;
  margin: 0;
  padding: 0.5em;
  position: sticky;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}
.nav-container-NavigationBar:after {
  content: '';
  position: absolute;
  width: 100%;
  height: 1em;
  background-color: rgb(243, 78, 78);
  top: 5.5em;
  left: 0;
}
/* ==== Waste Management IIT Tirupati BRAND NAME ==== */

.main-web-name-NavigationBar {
  font-family: 'Playfair Display', 'Roboto Slab', Arial, Helvetica, sans-serif;
  margin-bottom: 0.1em;
  height: 2.3em;
  padding: 0 0.5em;
  font-size: 200%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  background: linear-gradient(
    90deg,
    rgb(26, 146, 167),
    rgb(28, 72, 75),
    rgb(2, 26, 22)
  );
  background-size: 100%;
  background-clip: text;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
}

/* ==== NAVIGATION LINKS ==== */

/* NavLink Styling */
a.nav-link-NavigationBar,
.nav-dropdown-Navigation {
  position: relative;
  font-size: 120%;
  top: 1em;
  margin: 0 0.05em;
  white-space: nowrap;
  padding: 0.3em;
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgba(255, 255, 255, 0.5);
}

a.nav-link-Navigation:hover,
a.nav-link-Navigation.active {
  text-decoration: none;
  color: #f0f0f0;
}

